import {createApp, ref} from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import mitt, {Emitter} from "mitt";

export const emitter: Emitter = mitt();

import './style/index.scss'

import '../external-lib/dashboard/style/dashboard.default.less'
import '../external-lib/dashboard/style/dashboard.foot.less'
import '../external-lib/dashboard/style/dashboard.head.less'
import '../external-lib/dashboard/style/dashboard.nav.less'
import '../external-lib/dashboard/style/dashboard.nav.tiles.less'

declare var window: any;

// unregister service worker
navigator.serviceWorker.getRegistrations().then(function(registrations) {
    for(let registration of registrations) {
        registration.unregister()
    } })
window.myVue = createApp(App)
window.myVue.config.globalProperties.emitter = emitter


window.$globalConfig = {t: ''};
window.$globalUser = {t: ''};
window.myVue.config.globalProperties.$config = ref(window.$globalConfig);
window.myVue.config.globalProperties.$user = ref(window.$globalUser);

window.myVue.use(store).use(router).mount('#app')
// createApp(App).use(store).use(router).mount('#app')
